// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "BellotaText", sans-serif;
  --content-width: 1240px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-white: #fff;
  --color-black: #000;
  --color-blackGrey: #121212;
  --color-darkGrey: #2e2e2e; //2
  --color-grey: #4b4b4b; //1
  --color-lightGrey: #a8a8a8; //6
  --color-ultralightGrey: #d6d6d6;
  --color-darkOrange: #a66c4c;
  --color-lightBrown: #a87b62;
  --color-lightOrange: #cca78c; //4

  --color-red: #f52929;

  --color-greyDisable: #898989;

  --color-gradientPrimary: linear-gradient(90deg, #1c1c1c 0%, #816d5a 100%);

  --color--shadow: #1b1b1b; //2
}
