.footer {
  background-color: var(--color-blackGrey);
  padding-top: 40px;
  padding-bottom: 40px;

  @include mobile {
    padding-top: 20px;
    padding-bottom: 25px;
  }

  &__name {
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0.1em;
    color: #A8A8A8;
    margin: 0;

    @include mobile {
      font-size: 10px;
      line-height: 14px;
    }

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__year {}
}
