.portfolio {
  padding-top: 100px;
  padding-bottom: 100px;

  @include mobile {
    padding-top: 40px;
    padding-bottom: 10px;
  }

  &__title {
    text-align: center;
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 17px;
    }
  }

  &__btn {
    filter: drop-shadow(0px 0px 8px #494949);
    border: 1px solid var(--color-lightOrange);
    border-radius: 4px;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #D6D6D6;
    padding: 20px 65px 20px 30px;
    position: relative;
    transition: background-color .3s ease-in-out;

    &:hover {
      background-color: rgba(168, 123, 98, 0.8);
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 10%;
      transform: translateY(-50%);
      width: 16px;
      height: 18px;
      background-image: url(../img/load.svg);
      background-position: 92% bottom;
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }
}

.tabs {

  &__nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__nav-item {

    &:not(:last-child) {
      margin-right: 60px;

      @include mobile {
        margin-right: 30px;
      }
    }
  }

  &__content {
    margin-bottom: 105px;
  }

  &__nav-btn {
    font-weight: 400;
    font-size: 30px;
    line-height: 1.3;
    color: var(--color-greyDisable);
    text-transform: uppercase;
    transition: color .3s ease-in-out;

    &:hover {
      color: var(--color-lightOrange);
    }

    @include mobile {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__panel {
    display: none;
    justify-content: space-between;
    align-items: flex-start;

    @include mobile {
      flex-direction: column-reverse;
      align-items: center;
    }

    &:not(:last-child) {
      margin-bottom: 80px;

      @include mobile {
        margin-bottom: 60px;
      }
    }

    &:nth-child(n + 4) {
      display: none;
    }

    &--visible {
      display: flex !important;
    }

    &--visible-more {
      display: flex !important;
    }
  }

  &__panel-descr {
    width: calc(100% - 300px);
    margin-right: 55px;
    position: relative;
    text-align: right;

    @include mobile {
      width: 100%;
      text-align: center;
      margin-right: 0;
    }

    // &::before {
    //   content: "";
    //   position: absolute;
    //   bottom: 60px;
    //   left: 0;
    //   width: 100%;
    //   height: 1px;
    //   background-color: var(--color-lightGrey);
    // }
  }

  &__panel-title {
    font-weight: 700;
    font-size: 60px;
    color: var(--color-white);
    transition: color .3s ease-in-out;

    @include mobile {
      font-size: 24px;
      line-height: 34px;
    }

    &:hover {
      color: var(--color-lightOrange);
    }
  }

  &__panel-text {
    font-size: 30px;
    line-height: 40px;
    color: var(--color-lightGrey);
    margin: 0;
    margin-top: 17px;
    margin-bottom: 50px;

    @include mobile {
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 30px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 60px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: var(--color-lightGrey);

      @include mobile {
        bottom: 40px;
      }
    }
  }

  &__panel-cat {
    font-size: 30px;
    line-height: 40px;
    text-align: right;
    color: var(--color-lightGrey);
    margin: 0;
    text-transform: uppercase;

    @include mobile {
      font-size: 16px;
      line-height: 26px;
      text-align: center;
    }
  }
}

.tabs {
  @include tabs;
}
