.about {
  padding-top: 100px;
  background: var(--color-darkGrey);
  box-shadow: 0px 8px 10px var(--color--shadow), 0px -8px 10px var(--color--shadow);
  position: relative;
  z-index: 1;
  padding-bottom: 100px;

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__me {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 100px;

    @include mini-tablet {
      flex-direction: column-reverse;
      align-items: center;
    }

    @include mobile {
      margin-bottom: 40px;
    }

    &-img {
      @include mobile {
        width: 242px;
        margin: 0 auto;
      }
    }
  }

  &__me-right {
    max-width: calc((100% - 466px) - 8%);

    @include mini-tablet {
      max-width: 100%;
      text-align: center;
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 25px;
    }
  }

  &__subtitle {
    font-family: "AvenirNextCyr", sans-serif;
    font-weight: 700;
    font-size: 100px;
    line-height: 1;
    letter-spacing: 6px;
    color: var(--color-grey);
    text-transform: uppercase;
    margin-bottom: 25px;
    display: block;

    @include medium-tablet {
      font-size: 75px;
    }

    @include small-tablet {
      font-size: 46px;
    }

    @include mobile {
      font-size: 32px;
      line-height: 40px;
      text-align: center;
      margin-bottom: 12px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 30px;
    line-height: 1.33;
    color: var(--color-lightGrey);
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 10px;
      }
    }

    @include medium-tablet {
      font-size: 25px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 26px;
    }
  }

  &__text--color {
    color: var(--color-lightOrange);
  }

  &__link {
    color: var(--color-lightGrey);

    &:hover {
      color: var(--color-lightOrange);
    }
  }

  &__skills {
    text-align: center;
    margin-bottom: 100px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__skills-wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 35px;

    @include mobile {
      margin-top: 0;
    }
  }

  &__skills-list {
    text-align: left;

    @include mobile {
      &:last-child {
        margin-top: 10px;
      }
    }

    &:not(:last-child) {
      margin-right: 10px;
      margin-bottom: 20px;
    }
  }

  &__skills-item {
    font-weight: 400;
    font-size: 30px;
    line-height: 1.33;
    color: var(--color-lightGrey);
    padding-left: 20px;
    position: relative;

    @include mobile {
      font-size: 16px;
      line-height: 22px;
    }

    &:not(:last-child) {
      margin-bottom: 20px;

      @include mobile {
        margin-bottom: 10px;
      }

    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 8px;
      height: 8px;
      background-color: var(--color-lightOrange);
      border-radius: 50%;
    }
  }

  &__price {
    text-align: center;

    & .about__title {
      justify-content: center;
    }
  }
}
