.hero {
  background-image: url(../img/hero-bg.png);
  height: 100vh;
  min-height: 860px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include mini-tablet {
    min-height: 700px;
  }

  @include mobile {
    min-height: 600px;
  }

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    background-image: url(../img/hero-main.png);
    background-position: 92% bottom;
    background-size: auto 84%;
    background-repeat: no-repeat;
    filter: drop-shadow(36px 0px 80px rgba(0, 0, 0, 0.4));

    @include mini-tablet {
      justify-content: flex-start;
      align-items: center;
      background-image: none;
    }

    @include mobile {
      background-image: url(../img/hero-main.png);
      background-size: auto 56%;
      background-position: center bottom;
    }
  }

  &__descr {
    max-width: 580px;
    transform: translateY(12%);

    @include medium-tablet {
      max-width: 450px;
    }

    @include mini-tablet {
      max-width: 90%;
      transform: translateY(250px);
    }

    @include mobile {
      transform: translateY(100px);
    }
  }

  &__title {
    font-family: "AvenirNextCyr", sans-serif;
    font-weight: 700;
    font-size: 72px;
    line-height: 1.1;
    color: var(--color-white);
    margin: 0;

    @include mobile {
      font-size: 28px;
      line-height: 38px;
      text-align: center;
    }
  }

  &__subtitle {
    font-family: "BellotaText", sans-serif;
    font-weight: 400;
    font-size: 48px;
    line-height: 1.25;
    color: var(--color-lightGrey);
    display: block;
    margin-bottom: 10px;

    @include mobile {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 5px;
    }
  }

  &__text {
    font-family: "BellotaText", sans-serif;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.2;
    color: var(--color-lightGrey);
    display: block;
    margin-top: 20px;

    @include mobile {
      font-size: 16px;
      line-height: 26px;
      margin-top: 10px;
    }
  }
}
