.burger {
  @include burger;
  display: none;

  @include small-tablet {
    display: block;
  }
}

.header {
  left: 0;
  position: absolute;
  top: 0;
  z-index: 100;
  padding: 10px 0;
  width: 100%;
  background-color: var(--color-darkGrey);
  box-shadow: 0px 4px 8px var(--color--shadow);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: inline-block;
    width: 42px;
    height: 77px;

    @include mobile {
      width: 25px;
      height: 40px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__tel {
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: var(--color-white);
    position: relative;
    padding-left: 30px;
    transition: color .3s ease-in-out;

    @include mobile {
      font-size: 14px;
      line-height: 18px;
    }

    @include small-tablet {
      z-index: -1;
    }

    &:hover {
      color: var(--color-lightOrange);
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 15px;
      height: 24px;
      background-image: url(../img/tel.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include mobile {
        width: 10.5px;
        height: 16px;
      }
    }
  }
}

.nav {

  @include small-tablet {
    position: fixed;
    right: 0;
    top: 0;
    overflow-y: auto;
    padding: 150px 45px 45px;
    height: 100vh;
    background-color: var(--color-darkGrey);
    box-shadow: 2px 0px 8px #494949;
    transform: translateX(100%);
    transition: transform .3s ease-in-out;
  }

  &__list {
    flex-wrap: wrap;
    display: flex;
    align-items: center;

    @include small-tablet {
      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 60px;

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 2px;
    color: var(--color-white);
    text-transform: uppercase;
    transition: color .3s ease-in-out;

    &:hover {
      color: var(--color-lightOrange);
    }
  }
}

.menu--active {
  transform: translateX(0);
}
