.contacts {
  background: var(--color-darkGrey);
  box-shadow: 0px -8px 10px var(--color--shadow);
  padding-top: 100px;
  padding-bottom: 100px;

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__title {
    text-align: center;
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 45px;

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 30px;
    line-height: 40px;
    color: var(--color-lightGrey);
    margin: 0;
    font-style: normal;
    transition: color .3s ease-in-out;

    @include mobile {
      font-size: 16px;
      line-height: 26px;
    }

    &:hover:not(:first-child) {
      color: var(--color-lightOrange);
    }

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;

  &__item {
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
      width: 36px;
      height: 36px;
    }

    &:not(:last-child) {
      margin-right: 60px;

      @include mobile {
        margin-right: 25px;
      }
    }

    &-img {
      display: block;
      fill: var(--color-white);
      object-fit: cover;
      transition: fill .3s ease-in-out, background-color .3s ease-in-out;

      &--git {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        stroke: var(--color-black);
        fill: var(--color-black);
        background-color: var(--color-white);

        @include mobile {
          width: 35px;
          height: 35px;
        }
      }

      &--fl {
        width: 35px;
        height: 29px;

        @include mobile {
          width: 21px;
          height: 17px;
        }
      }

      &--telegram {
        width: 29px;
        height: 27px;
        transform: translateX(-2px);

        @include mobile {
          width: 17px;
          height: 16px;
        }
      }

      &--whatsup {
        width: 37px;
        height: 37px;

        @include mobile {
          width: 23px;
          height: 23px;
        }
      }

      &--skype {
        width: 26px;
        height: 26px;

        @include mobile {
          width: 16px;
          height: 16px;
        }
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: var(--color-black);
    filter: drop-shadow(2px 0px 8px #494949);
    border-radius: 50%;
    transition: filter .3s ease-in-out;

    &:hover {
      filter: drop-shadow(2px 0px 8px #cca78c);
    }

    &:hover .social__item-img {
      fill: var(--color-lightOrange);
    }

    &:hover .social__item-img--git {
      fill: var(--color-black);
      background-color: var(--color-lightOrange);
    }
  }
}

.social__item-img--telegram {

  --star-fill: #EAEAEA;
}
